export default {
	data() {
		return {
			// 服务器地址
			baseUrl: getApp().globalData.baseUrl,
			tempDesc: this.description
		}
	},
	props:{
		tempHeight: Number,
		tempWidth: Number,
		tempCoverUrl: String,
		description: String,
		tempVideoUrl: String,
		duration: Number,
		isShowCoverImage: Boolean,
		_isSelectVideo: Boolean,
	},
	methods: {
		// 上传视频相关
		selectVideo() {
			this.$emit('loadProgress')
			this.$emit('isLoading', true)
			uni.chooseVideo({
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.$emit('isLoading', false)
					if (res.duration > 21) {
						uni.showToast({
							title: '视频长度不能超过20秒',
							icon: 'none',
							duration: 2000
						})
						return
					} else if (res.duration < 4) {
						uni.showToast({
							title: '视频长度太短，请上传超过3秒的视频',
							icon: 'none',
							duration: 2000
						})
						return
					} else {
						console.log(res);
						this.result = [{
							'tempHeight': res.height,
							'tempWidth': res.width,
							'tempVideoUrl': res.tempFilePath,
							'duration': res.duration,
							'description': ''
						}]
						this.$emit('setMessage',this.result)
						this.$emit('isSelectVideo')
						uni.showToast({
							title: '视频读取成功！',
							icon: 'none',
							duration: 1000
						})
					}

				},
				fail: () => {
					this.$emit('isLoading', false)
					uni.showToast({
						title: '视频读取失败！',
						icon: 'none',
						duration: 1000
					})
				}
			})
		},
		isSetCoverImage() {
			if (this.isShowCoverImage === true) {
				this.$emit('setCoverImage', '')
			}
			console.log('==================');
			this.$emit('isSetCoverImage')
		},
		setCoverImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				success: (res) => {
					this.$emit('setCoverImage', res.tempFilePaths[0])
				}
			})
		},
		handleNext() {
			console.log(this.result);
			console.log(this.tempDesc);
			this.result[0].description = this.tempDesc
	
			this.$emit('setMessage',this.result)
			this.$emit('nextStep')
		}
	}
}
